import React, { Component } from 'react';
import { Tabs, Tab, Grid, Cell, Card, CardTitle, CardText, CardActions, Button, CardMenu, IconButton } from 'react-mdl';

class Projekty extends Component {
    constructor(props) {
        super(props);
        this.state = { activeTab:0 };
    }
    
    toggleCategories(){
        if(this.state.activeTab ===0) {
            return(
            <div className="projekty-grid">    
               {/* Gra w statki */}
                    <Card shadow={5} style={{minWidth: '450', margin: 'auto'}}>
                    <CardTitle style={{color: 'black', height: '176px', background: 'url(https://jaki-jezyk-programowania.pl/img/javascript.png)  center / cover'}}>Gra w statki
                    </CardTitle>
                    <CardText>
                    Gra stworzona za pomocą języka JavaScript. Położenie okrętów na planszy generowane jest losowo za każdym razem, gdy odświeżymy stronę. Na planszy znajdują się trzy okręty, każdy z nich zajmuje 3 pola. Gra napisana w oparciu u książkę "Rusz głową! Programowanie w Javascript" E. T. Freeman, E. Robson.
                    </CardText>
                    <CardActions border>
                        <Button colored><a className="projekty-link" href="https://github.com/szczypkamaciek/Gra-w-statki" rel="noopener noreferrer" target="_blank">Github</a></Button>
                    </CardActions>
                    <CardMenu style={{color: '#fff'}}>
                        <IconButton name="share" />   
                    </CardMenu>
                </Card>
                {/* Sprawdzanie wyników Lotto */}
                    <Card shadow={5} style={{minWidth: '450', margin: 'auto'}}>
                    <CardTitle style={{color: 'black', height: '176px', background: 'url(https://jaki-jezyk-programowania.pl/img/javascript.png)  center / cover'}}>Wyniki Lotto
                    </CardTitle>
                    <CardText>
                   Aplikacja po podaniu sześciu liczb, porównuje je z ostatnimi wynikami Lotto.
                    </CardText>
                    <CardActions border>
                        <Button colored><a className="projekty-link" href="https://github.com/szczypkamaciek/Lotto" rel="noopener noreferrer" target="_blank">Github</a></Button>
                    </CardActions>
                    <CardMenu style={{color: '#fff'}}>
                        <IconButton name="share" />   
                    </CardMenu>
                </Card>
                
            </div>
            )
        } else if(this.state.activeTab === 1) {
            return(
            <div className="projekty-grid">
                {/* Kalkulator */}
                        <Card shadow={5} style={{minWidth: '450', margin: 'auto'}}>
                        <CardTitle style={{color: 'black', height: '176px', background: 'url(https://zapodaj.net/images/1ca2858970561.jpg)  center / cover'}}>Prosty kalkulator
                        </CardTitle>
                        <CardText>
                        Prosty kalkulator stworzony z wykorzystaniem języka HTML/CSS oraz Javascript. Wygląd zaczerpnięty z kalkulatora Apple.
                        </CardText>
                        <CardActions border>
                            <Button colored><a className="projekty-link" href="https://github.com/szczypkamaciek/Kalkulator" rel="noopener noreferrer" target="_blank">Github</a></Button>
                        </CardActions>
                        <CardMenu style={{color: '#fff'}}>
                            <IconButton name="share" />   
                        </CardMenu>
                    </Card>    
            </div>
            )
        } else if(this.state.activeTab === 2) {
            return(
            <div className="projekty-grid">
                {/* Strona */}
                        <Card shadow={5} style={{minWidth: '450', margin: 'auto'}}>
                        <CardTitle style={{color: 'black', height: '176px', background: 'url(http://akra.net/wp-content/uploads/2017/01/reactlogo2.png)  center / cover'}}>Moje portfolio - strona www
                        </CardTitle>
                        <CardText>
                        Na potrzeby nauki biblioteki React.js stworzyłem tą stronę internetową, która zarazem pełni rolę mojego portfolio. Wykorzystałem bilbioteki takie jak react-mdl, react-router. Wszystkie potrzebne ikony pozyskałem ze strony <a className="projekty-link" href="https://fontawesome.com/">Font Awesome</a>.
                        </CardText>
                        <CardActions border>
                            <Button colored><a className="projekty-link" href="https://github.com/szczypkamaciek/Kalkulator" rel="noopener noreferrer" target="_blank">Github</a></Button>
                        </CardActions>
                        <CardMenu style={{color: '#fff'}}>
                            <IconButton name="share" />   
                        </CardMenu>
                    </Card>    
            </div>
            )
        }
    }
    
    
    
    render () {
        return(
        <div className="category-tabs">
            <Tabs activeTab ={this.state.activeTab} onChange={(tabId) => this.setState({ activeTab: tabId })} ripple>
                <Tab>JavaScript</Tab>
                <Tab>HTML/CSS</Tab>
                <Tab>React.JS</Tab>  
            </Tabs>
            
                <Grid>
                    <Cell col={12}>
                        <div className="content">{this.toggleCategories()}</div>
                    </Cell> 
                </Grid>
        </div>
        )
    }
}

export default Projekty;