import React, { Component } from 'react';
import { Grid, Cell, List, ListItem, ListItemContent } from 'react-mdl';

class Kontakt extends Component {
    render () {
        return(
        <div className="contact-body">
            <Grid className="contact-grid">
                <Cell col={6}>
                    <h2>Maciej Szczypka</h2>
                    <img
                        src='https://zapodaj.net/images/4ce6e82d41058.png'
                        alt='avatar'
                        style={{height: '250px'}}
                    />
                    <p style={{width: '75%', margin: 'auto', paddingTop: '2em'}}>W razie jakichkolwiek pytań, po prostu zadzwoń :)</p>
                </Cell>
                <Cell col={6}>
                    <h2>Kontakt</h2>
                    <hr/>
                    <div className="contact-list">
                        <List>
                            <ListItem>
                                <ListItemContent style={{fontSize: '27px', fontFamily: 'Anton'}}>
                                    <i className="fa fa-phone-square" aria-hidden="true"/>
                                    +48 695 029 890
                                </ListItemContent>
                            </ListItem>
                            <ListItem>
                                <ListItemContent style={{fontSize: '27px', fontFamily: 'Anton'}}>
                                    <i className="fa fa-envelope" aria-hidden="true"/>
                                    szczypka.maciek@gmail.com
                                </ListItemContent>
                              </ListItem>
                            <ListItem>
                                <ListItemContent style={{fontSize: '27px', fontFamily: 'Anton'}}>
                                    <i className="fab fa-facebook-square" aria-hidden="true"/>
                                    /maciek.szczypka.7
                                </ListItemContent>
                            </ListItem>
                        </List>
                    </div>
                </Cell>  
            </Grid>
        </div>
        )
    }
}

export default Kontakt;