import React, { Component } from 'react';
import { Grid, Cell } from 'react-mdl';

class Omnie extends Component {
    render () {
        return(
        <div className="omnie-body">
            <Grid className="omnie-grid">
                <Cell col={10}>
                    <h2>O mnie</h2>
                    <hr/>
                    <p style={{width: '75%', margin: 'auto', paddingTop: '1em'}}>Skoro trafiłeś na tą stronę to w 95% chciałbyś mnie bliżej poznać. Nie ma problemu :)</p>
                    <p style={{width: '75%', margin: 'auto', paddingTop: '1em'}}>Mam 24 lata i pochodzę z Czechowic-Dziedzic - miasta leżącego obok Bielska-Białej. Obecnie mieszkam w Krakowie, gdzie studiuję i pracuję. Pochodzę z Beskidu Śląskiego, dlatego też bardzo lubię chodzić po górach. Gram na gitarze już od szkoły podstawowej oraz bardzo lubię czytać książki z dziedziny fantasy. </p>
                    <h2>Dlaczego ja?</h2>
                    <hr/>
                    <p style={{width: '75%', margin: 'auto', paddingTop: '1em'}}>Pomimo codziennych obowiązków i studiowania zawsze znajdę czas programowanie oraz poszerzanie swojej wiedzy. Oczywiście wchodzi w to również moja determinacja i chęć nauczenia się jak najwięcej. Ukończone przeze mnie studia techniczne nauczyły mnie pokory oraz uporu w dążeniu do wyznaczonego celu. Zdobyte doświadczenie pozwoliło mi uwierzyć we własne siły, dzięki czemu do wszelkiego rodzaju problemów podchodzę z dużą dozą pewności siebie.</p>
                </Cell>
            </Grid>    
        </div>
        )
    }
}

export default Omnie;