import React, { Component } from 'react';
import { Grid, Cell } from 'react-mdl';
import Education from './szkola';
import EducationOther from './szkolainne';

class Cv extends Component {
    render () {
        return(
        <div>
            <Grid>
                <Cell col={4}>
                    <div style={{textAlign: 'center'}}>
                        <img                                            src="https://zapodaj.net/images/4ce6e82d41058.png"
                        alt="avatar"
                        style={{height: '250px'}}
                        />
  
                    </div>
                    <h2 style={{paddingTop: '1em'}}>Maciej Szczypka</h2>
                    <h4 style={{color: 'grey'}}>Pobierz moje CV   <a href="https://drive.google.com/open?id=1a8rnSwnaW3Xqw3vK9Rz2gM0gJfMwENGb" rel="noopener noreferrer" target="_blank"><i className="fas fa-file-download"/></a>
                    
                    </h4>
                    <hr style={{borderTop: '3px solid #833fb2', width: '50%'}}/>
                    <p style={{textAlign: 'justify'}}>Jestem studentem pierwszego roku Informatyki na AGH w Krakowie. Skończyłem również inne studia na tej uczelni, lecz z powodu ciężkiej sytuacji na rynku pracy postanowiłem się przebranżowić.</p>
                    <p style={{textAlign: 'justify'}}>Obecnie poszukuję stażu związanego ze stanowiskiem Frontend Developera. Chciałbym w ten sposób zdobyć cenne doświadczenie pod okiem ekspertów oraz rozbudować moje umiejętności.</p>
                </Cell> 
                <Cell  className="cvright" col={8}>
                    
                    <h2>Edukacja</h2>
                    
                    <Education 
                        startYear={2013}
                        endYear={2017}
                        schoolName="Akademia Górniczo-Hutnicza im. Stanisława Staszica w Krakowie"
                        schoolDescription="Studia inżynierskie pierwszego stopnia, Wydział: Górnictwa i Geoinżynierii, kierunek: Górnictwo i Geologia. Studia ukończone z wynikiem 4.0"
                        />
                    <Education 
                        startYear={2017}
                        endYear={2018}
                        schoolName="Akademia Górniczo-Hutnicza im. Stanisława Staszica w Krakowie"
                        schoolDescription="Studia magisterskie drugiego stopnia, Wydział: Geologii, Geofizyki i Ochrony Środowiska, kierunek: Górnictwo i Geologia - Kartografia Geologiczna. Studia ukończone z wynikiem 4.5"
                        />
                     <Education 
                        startYear={2018}
                        endYear={2022}
                        schoolName="Akademia Górniczo-Hutnicza im. Stanisława Staszica w Krakowie"
                        schoolDescription="Studia inżynierskie pierwszego stopnia w trybie niestacjonarnym, Wydział: Informatyki Elektroniki i Telekomunikacji, kierunek: Informatyka."
                        />
                    <hr style={{borderTop: '3px solid #e22947'}}/>
                    
                    <h2>Kursy</h2>
                    
                    <EducationOther
                        startYear={2018}
                        schoolName="„Programowanie w Javascript. Rusz Głową!” - Eric T. Freeman, Elizabeth Robson"
                        schoolDescription="Dzięki też książce poznałem składnię i podstawowe elementy języka JavaScript. Nauczyłem się tworzyc obiekty oraz obsługiwac zdarzenia. Na jej podstawie powstała „Gra w statki” dostępna w sekcji Projekty."
                        />
                    <EducationOther
                        startYear={2018}
                        schoolName="„Kurs Tworzenia Stron WWW w HTML i CSS od Podstaw do Eksperta” -  Arkadiusz Włodarczyk"
                        schoolDescription="Kurs dostępny na platformie udemy.com. Dzięki temu kursowi nauczyłem się tworzyć strony internetowe z wykorzystanie HTML5/CSS3. Nie jest mi obce projektowanie stron w technice RWD."
                        />
                     
                    <EducationOther
                        startYear={2018}
                        schoolName="„React 16 - The Complete Guide (incl. React Router 4 & Redux)” -  Maximilian Schwarzmüller"
                        schoolDescription="KURS W TRAKCIE REALIZACJI. Kurs dostępny na platformie udemy.com. Z tego kursu pozyskuję wiedzę na temat biblioteki React.js. Dzięki wykorzystaniu dotychczasowej wiedzy zdobytej w tym kursie powstała strona internetowa mojego portfolio na, której obecnie jesteś."
                        />
                     <hr style={{borderTop: '3px solid #e22947'}}/>
                    
                </Cell>
            </Grid>
        </div>
        )
    }
}

export default Cv;