import React, { Component } from 'react';
import { Grid, Cell } from 'react-mdl';

class StronaGlowna extends Component {
    render () {
        return(
        <div style={{width: '100%', margin: 'auto'}}>
            <Grid className="glowna-grid">
                <Cell col={12}>
                <img 
                    src="https://zapodaj.net/images/4ce6e82d41058.png"
                    alt="avatar"
                    className="avatar-img"
                />
                    <div className="banner-text">
                        <h1>Student Informatyki</h1>
                        <hr/>
                        <p>HTML/CSS | JavaScript | React | GIT</p>
                        
                    <div className="social-links">
                        
                        {/* LinkedIn */}
                        <a href="https://www.linkedin.com/in/maciej-szczypka-0bb549174" rel="noopener noreferrer" target="_blank">
                            <i className="fab fa-linkedin" />
                        </a>
                        
                        {/* GitHub */}
                        <a href="https://github.com/szczypkamaciek" rel="noopener noreferrer" target="_blank">
                            <i className="fab fa-github-square" />
                        </a>
                        
                        {/* Facebook */}
                        <a href="https://www.facebook.com/maciek.szczypka.7" rel="noopener noreferrer" target="_blank">
                            <i className="fab fa-facebook-square" />
                        </a>
                        
                    </div>
                    </div>
                </Cell>
            </Grid>
        </div>
        )
    }
}

export default StronaGlowna;